'use client'
import { useEffect, useState } from 'react'
import { useRouter } from 'next/navigation'
import Box from '@mui/material/Box'
import Grid from '@mui/material/Grid'
import Paper from '@mui/material/Paper'
import Button from '@mui/material/Button'
import Typography from '@mui/material/Typography'

export default function Dashboard(){
  const router = useRouter()
  const [user, setUser] = useState('Pengguna')

  useEffect(()=>{
    if (typeof window === 'undefined') return
    const auth = localStorage.getItem('sigap_auth')
    if (!auth){ router.push('/login'); return }
    setUser(localStorage.getItem('sigap_user') || 'Pengguna')
  }, [router])

  function logout(){
    localStorage.removeItem('sigap_auth')
    localStorage.removeItem('sigap_user')
    router.push('/login')
  }

  return (
    <Box className="dashboard">
      <Box sx={{display:'flex',justifyContent:'space-between',alignItems:'center',mb:2}}>
        <Typography variant="h5">Dashboard</Typography>
        <Box sx={{display:'flex',gap:1,alignItems:'center'}}>
          <Typography>Halo, <strong>{user}</strong></Typography>
          <Button variant="outlined" onClick={logout}>Logout</Button>
        </Box>
      </Box>

      <Grid container spacing={2}>
        <Grid item xs={12} sm={6} md={4}>
          <Paper sx={{p:2,height:120,display:'flex',alignItems:'center',justifyContent:'center'}}>Tugas Hari Ini</Paper>
        </Grid>
        <Grid item xs={12} sm={6} md={4}>
          <Paper sx={{p:2,height:120,display:'flex',alignItems:'center',justifyContent:'center'}}>Kalender</Paper>
        </Grid>
        <Grid item xs={12} sm={6} md={4}>
          <Paper sx={{p:2,height:120,display:'flex',alignItems:'center',justifyContent:'center'}}>Catatan</Paper>
        </Grid>
      </Grid>
    </Box>
  )
}
